<template>
    <v-container>
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.replace({name: 'settingsAccount'})">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{changeEmailText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card class="pa-5" style="margin-top: 60px;">
            <v-form v-model="validForm">
                <v-text-field
                        v-model="email"
                        :rules="[rules.required, rules.email]"
                        :label="emailText"
                        required
                ></v-text-field>
            </v-form>
            <v-btn block color="primary" style="margin-top: 10px" @click="updateEmail" :disabled="buttonIsDisabled">
                {{updateText}}
            </v-btn>
        </v-card>

        <v-dialog
                v-model="dialog"
                max-width="500px"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <p style="margin-bottom: 7px">{{yourEmailHasBeenUpdatedText}}</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="$router.go(-2)">{{continueText}}</v-btn>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions} from "vuex";

    export default {
        name: "SettingsAccountEditEmail",
        data() {
            return {
                buttonIsDisabled: false,
                validForm: false,
                snackbar: false,
                snackbarText: null,
                dialog: false,
                email: this.$store.getters['user/getEmail'],
                rules: {
                    required: value => !!value || this.$i18n.t('messages.RULE_TEXT_MANDATORY_FIELD'),
                    email: value => {
                        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                        return pattern.test(value) || this.$i18n.t('messages.TEXT_EMAIL_NOT_VALID')
                    }
},
                changeEmailText: null,
                emailText: null,
                closeText: null,
                continueText: null,
                updateText: null,
                yourEmailHasBeenUpdatedText: null
}
        },
        methods: {
            ...mapActions('user', ['updateAccountEmail']),

            updateEmail: function () {
                let self = this;
                if (self.email != self.$store.getters['user/getEmail']) {
                    let resultUpdateAccountEmail = self.updateAccountEmail(self.email);

                    resultUpdateAccountEmail.then(() => {
                        self.dialog = true;
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                } else {
                    self.$router.replace({name: 'settingsAccount'});
                }

            }
        },
        created() {
            this.changeEmailText = this.$i18n.t('messages.TEXT_CHANGE_EMAIL');
            this.emailText = this.$i18n.t('messages.TEXT_EMAIL_DIRECTION');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.continueText = this.$i18n.t('messages.TEXT_TO_CONTINUE');
            this.updateText = this.$i18n.t('messages.TEXT_UPDATE');
            this.yourEmailHasBeenUpdatedText = this.$i18n.t('messages.TEXT_YOUR_EMAIL_HAS_BEEN_UPDATED');
        }
    }
</script>

<style scoped>

</style>